.cart--main {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-bottom: 25px;
}

.cart {
    background: #f4f4f4;
    padding: 25px;
    border-radius: 33px;
    width: 75%;
    min-width: 520px;
    max-width: 1000px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
}

@media (max-width: 900px) {
    .cart-items {
        min-width: 100%;
    }
    .cart {
        min-width: 300px;
    }
}

.cart > p {
    text-align: center;
    margin-bottom: 12px;
    font-weight: bold;
}

.cart-items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}

.cart-options {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    gap: 15px;
    margin-top: 15px;
    max-width: 850px;
    min-width: 520px;
}

.ship-promo,
.total-checkout {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
}

.shipping,
.promo,
.total {
    display: flex;
    gap: 12px;
    align-items: center;
}

.shipping > select,
.promo > input {
    width: 150px;
}

select {
    -webkit-appearance: menulist-button;
}

input {
    border: 1px solid rgb(137, 137, 137);
}


.cart-option-title {
    font-weight: bold;
    font-size: 1.1rem;
}

.no-items {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px
}

.back-to-shop {
    border: none;
    border-radius: 33px;
    padding: 8px 12px;
    background:  #B2BCAA;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
}

.back-to-shop:hover {
    background: #bfbfbf;
}